h1{
  font-size:2em;
  color:darkgray;
  text-align:center;
}

.calculator{
  width:100%;
  height:560px;
  background-color:lightgray;
  border-radius:15px;
  border:solid 2px lightgray;
}

.affichage{
  width:100%;
  height:65px;
  background-color:ghostwhite;
}

.touche{
  width:80px;
  height:80px;
  border-radius: 100%;
  background-color:lightblue;
  font-size:1.5em;
}

.range1, .range2, .range3, .range4{
  width:100%;
  margin-top:10px;
  margin-bottom:10px;
  margin-left:auto;
  margin-right:auto;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.signe{
  background-color:lightsalmon;
}

.range5{
  width:100%;
  display:flex;
  justify-content: center;
}

.reset{
  width:120px;
  height:60px;
  border-radius: 20px;
  font-size:1.5em;
}


@media screen and(min-width:768px){

  .calculator {
    width: 25%;
    height: 520px;
    margin:0 auto;
    overflow:hidden;
  }

  .touche{
    width:60px;
    height:60px;
    border-radius: 100%;
    background-color:lightblue;
    font-size:1.5em;
  }

  .range1, .range2, .range3, .range4{
    margin-top:25px;
    margin-bottom: 25px;
  }


}